import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'
import 'muse-ui/dist/theme-carbon.css' //  carbon 
Vue.use(MuseUI)  // Vue.use() Vue.js  install  install 

new Vue({
  el: '#app', //
  router,
  store,   //
  template: '<App/>',  //
  components: { App }  // 
})



